.itekblock {
  padding-top: 10px;
  text-align: center;
  width: 100%; }
  .itekblock .reass-item {
    display: inline-block;
    text-align: center;
    vertical-align: top; }
  .itekblock .block-title {
    font-weight: bold; }
  .itekblock .block-icon,
  .itekblock .block-icon img,
  .itekblock .block-icon svg {
    height: 70px; }

@media (max-width: 575px) {
  .itekblock {
    text-align: left; }
  .itekblock .block-icon {
    display: inline-block;
    height: 35px;
    float: left;
    margin: 10px;
    width: 35px; }
  .itekblock .block-icon img,
  .itekblock .block-icon svg {
    height: 35px;
    width: 35px; }
  .itekblock .block-title {
    display: inline-block;
    height: 35px;
    max-width: calc(100% - 55px);
    padding-top: 10px; } }

.itekblock_product {
  margin-top: 25px; }
  .itekblock_product .item-product {
    display: inline-block;
    height: 35px;
    float: left;
    margin-right: 5px;
    padding-top: 5px;
    width: 35px; }

.itekblock_product .item-product img,
.itekblock_product .item-product svg {
  height: 35px;
  width: 35px; }

.itekblock_product p.block-title {
  line-height: 40px; }

/* Specific Checkout */
body#checkout .container-itekblock {
  min-height: auto; }
